}

.user-login-form-input-style-password button {
	outline: none;
	border: 0;
	background-color: transparent;
	color: #6b6b6b;
}
/************************************************************/
.user-login-form-btn {
	border: 0;
	outline: none;
	background-color: #1d3557;
	padding: 20px 20px;
	font-size: 16px;
	color: #f1faee;
	box-shadow: 0px 5px 10px 1px rgba(155, 155, 155, 0.3);
	transition: all 0.2s linear;
	position: relative;
}

.user-login-form-btn > span {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
}

.user-login-form-btn:hover {
	color: #1d3557;
	background-color: #fff;
	border: 2px solid #1d3557;
}

.user-login-form-btn:active {
	color: #1d3557;
	box-shadow: 0px 2px 10px 1px rgba(155, 155, 155, 0.8);
}

.user-login-form-btn::after {
	content: "";
	width: 100%;
	height: 100%;
	background-color: #a8dadc;
	border-radius: 25px;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	transition: all 0.4s ease;
}

.user-login-form-btn:active::after {
	transform: scale(1.2);
	opacity: 0;
}

