/* *******************SIGN UP FORM******************* */

.user-signup-form {
	margin-top: 20px;
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	width: 80%;
	z-index: 11;
	animation: FadeInSignUp 0.5s ease;
}

.user-signup-form-input-info {
	font-weight: 300;
	font-size: 12px;
	color: #457b9d;
	text-align: center;
	margin-bottom: 10px;
}

.user-signup-form label {
	font-size: 16px;
	padding-left: 10px;
	margin-bottom: 2px;
	font-weight: 300;
	color: #457b9d;
}

.user-signup-form-input-style input {
	color: #1d3557;
	padding: 10px;
	font-size: 16px;
	font-family: "Oswald";
	font-weight: 300;
	outline: none;
	border: 0;
	width: 90%;
	background-color: transparent;
}

.user-signup-form-input-style input[name="password"],
input[name="cpassword"] {
	width: 80%;
}

.user-signup-form-input-style > button[name="btn-showpw"] {
	outline: none;
	background-color: transparent;
	border: 0;
}

.user-signup-form-input-style {
