}

.user-signup-form-btn:hover {
	color: #1d3557;
	background-color: #fff;
	border: 2px solid #1d3557;
}

.user-signup-form-btn:active {
	color: #1d3557;
	box-shadow: 0px 2px 10px 1px rgba(155, 155, 155, 0.8);
