/* *************************************************** */

@keyframes FadeInLogin {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes FadeInSignUp {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
