.budget-new-form {
	background-color: #1d3557;
	border-radius: 25px;
	width: 95%;
	height: 550px;
	position: relative;

	box-shadow: 2px 5px 5px 2px #1d355731;
	animation: FrmMoveIn 0.6s ease;

	display: flex;
	justify-content: center;
	align-items: flex-start;
}

form {
	margin: 0;
	padding: 0;
	width: 100%;
}

.budget-new-form-btn-close {
	outline: none;
	border: 0;
	background-color: transparent;
	position: absolute;
	right: 25px;
	top: 10px;

	border-radius: 50%;
	background-color: white;
	width: 35px;
	height: 35px;
	color: #1d3557;
	font-size: 12px;
	font-family: "Roboto";
	font-weight: 700;

	transition: all 0.3s ease;
}

.budget-new-form-btn-close:hover {
	border: 2px solid white;
	background-color: transparent;
	color: white;
}

.budget-new-form-btn-close:active {
	transform: translateY(5px);
}

.budget-new-form-field-container {
	margin-top: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

	width: 90%;
}

/* FIELDS */
/********************************************/
.budget-new-form-field {
	background-color: transparent;
	border-bottom: 2px solid #fff;
	margin: 20px 0;
	transition: all 0.1s linear;
	position: relative;
	width: 60%;

	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-direction: row;
}

.btn-budget-type-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	flex-basis: 100%;
	height: 50px;
}

.btn-budget-type-container > button[name="btn-budget-type"] {
	outline: none;

	flex-basis: 100%;
	height: 100%;
	font-size: 16px;
	font-family: "Roboto";
	margin: 2px;
	transition: all 0.3s linear;
}

.btn-budget-type-exp {
	border: 2px solid #e63946;
	background: transparent;
	color: #e63946;
}

.btn-budget-type-exp-active {
	background-color: #e63946;
	color: #fff;
}

.btn-budget-type-inc {
	border: 2px solid #fff;
	background: transparent;
	color: #fff;
}

.btn-budget-type-inc-active {
	background-color: #fff;
	color: #1d3557;
}

.budget-new-form-field input {
	color: #fff;
	padding: 10px;
	font-size: 16px;
	font-family: "Oswald";
	font-weight: 300;
	outline: none;
	border: 0;
	width: 80%;
	background-color: transparent;
}

.budget-new-form-field input::placeholder {
	color: #fff;
	font-weight: 500;
}

.budget-new-form-field select {
	color: #fff;
	padding: 10px;
	font-size: 16px;
	font-family: "Oswald";
	font-weight: 300;
	text-align: center;
	outline: none;
	border: 0;
	width: 100%;
	background-color: transparent;
}

.budget-new-form-field span {
	color: #fff;
}

.budget-new-form-field select option {
	color: black;
	text-align: center;
}

.budget-new-form-field textarea {
	color: #fff;
	padding: 10px;
	font-size: 16px;
	font-family: "Oswald";
	font-weight: 200;
	outline: none;
	border: 0;
	width: 100%;
	background-color: transparent;
}

.budget-new-form-field textarea::placeholder {
	color: #fff;
	font-weight: 500;
}

.budget-new-form-field:focus-within {
	border: 2px solid #fff;
}

.budget-new-form-field input:disabled {
	color: #bebebe;
}

/* Add Button */
/* ******************************************* */

.budget-new-form-field-btn-add {
	outline: none;
	border: 0;
	position: relative;

	border-radius: 25px;
	margin-top: 20px;
	width: 200px;
	height: 50px;
	background-color: #fff;
	color: #1d3557;

	font-family: "Roboto";
	font-weight: 500;
	font-size: 16px;
	text-align: center;
	line-height: 1;

	transition: all 0.1s ease-in-out;
}

.budget-new-form-field-btn-add:active {
	transform: translateY(2px);
}

.budget-new-form-field-btn-add:after {
	content: "";
	width: 100%;
	height: 100%;
	border-radius: 50%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	opacity: 0.3;
	background-color: #ffffff;

	transition: all 0.2s linear;
}

.budget-new-form-field-btn-add:active::after {
	transform: scaleX(5);
	transform: scaleY(2);
	opacity: 0;
}

/********************************************/

@keyframes BGFadeIn {
	0% {
		/* background-color: rgba(0, 0, 0, 0); */
	}
	100% {
		background-color: rgba(0, 0, 0, 0.5);
	}
}
